* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* .content {
  height: 80vh;
} */

.foot {
  position: fixed;
  bottom: 0;
  width: 100%;
}
.navbar {
  display: flex;
  justify-content: space-between;
  background-color: aqua !important;
  padding: 10px;
  /* margin: 10px; */
  /* border-radius: 20px; */
  box-shadow: 5px 5px 10px black;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.navbar span {
  margin: 10px;
  font-weight: bold;
  font-size: 120%;

}

.uname-logout {
  display: flex;
  justify-content: space-between;
}

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}


.filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}





.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

.switch-icon {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}


